@charset "utf-8";

.yellow *{
  margin: 0; padding: 0;
  color: #333;
}
.yellow{
  width: 1200px;
  margin: 0 auto;
}
h2{text-align: center;}
a{text-decoration: none;}
ul, li{list-style: none;}
img{width: 250px;}
.tab_menu2{
  width: 600px;
  display: flex;
  justify-content: space-around;
  margin: 0 auto;
  cursor: pointer;
}
.tab{color: #333;}
.tab.act{
  color: rgb(192, 192, 0);
}

.content_menu2{
  width: 1200px;
  margin: 50px 0px auto;
  display: flex;
  flex-wrap: wrap;
}
.content2{
  white-space: none;
  display: none;
}
.content2.act2{display: block;}
.content2 li{width: 250px; height: 300px; float: left;
margin: 0px 23.7px;
}
.con{position: relative;}
.sub{
  font-size: 14px;
  font-weight: bold;
  margin-top: 10px;
}
.price{
  position: absolute;
  color: #f00;
  right: 0px; bottom: 0px;
  font-size: 20px;
  float: right;
  margin-top: 30px;
  font-weight: bold;

}
.won{
  color: #f00;
  font-size: 10px;
}