*{margin: 0; padding: 0;}

.App > h2{
  color: #f00;
  text-align: center;
}
.App{text-align: center; position: fixed; z-index: 1000; background: rgb(0, 0, 255,.6);
width: 100%; color: #fff; top: 0px;}

.App > h1{
  font-size: 20px;
  padding: 10px 0px
}

h3{color: rgb(70, 70, 255); margin-bottom: 10px;  border-bottom:3px solid rgb(0, 0, 255,.6);}
.title3{color: #f00; margin: 30px 0px; text-align: center;}
ul, ol, dl{
  margin-bottom: 20px;
  line-height: 30px;
}
dt{font-weight: bold; color: green;}
li, p, dd{transition: 0.3s; font-size: 14px;}
li:hover, p:hover, dd:hover{color:#333; background: rgba(130, 209, 255, 0.6);} 
p{line-height: 20px; margin-bottom: 10px; margin-left: 10px;}

main{margin-top: 50px; padding-left: 10px;}
.footer{
  background: #333;
  color: #fff;
  text-align: center;
  line-height: 40px;
}



address{font-style: normal;}